import request from '@/common/js/fetch'

export function sendEmail(email,mailType) {
    const data = {
        mailType, //signIn register updateEmail
        email
    }
    return request({
      url: '/sendMail',
      method: 'post',
      params:data
    })
  }


  export function sendSms(mobile,smsType) {
    const data = {
        smsType, //signIn register reMobile
        mobile
    }
    return request({
      url: '/sendSms',
      method: 'post',
      params:data
    })
  }

  export function verifyCode(params) {
    return request({
      url: '/verifyCode',
      method: 'get',
      params
    })
  }



